
import { defineComponent } from "@vue/runtime-core"

export default defineComponent({
    name: 'Pagination',
    data: () => ({
        pageNumber: 1
    }),
    emits: ['handlePaging'],
    methods: {
        changePage(num:number){
            this.pageNumber = num
            this.$emit('handlePaging', {
                page: num,
            })
        },
        nextPrev(num: number){
            let newNum = this.pageNumber + num 
            this.pageNumber = newNum
            this.$emit('handlePaging', {
                page: newNum,
            })
        }
    }
})
